.hero {
  position: relative;
  max-width: unset !important; }

.hero__figure {
  width: 100vw;
  position: relative;
  margin-left: -50vw;
  left: 50%; }
  .hero__figure::after {
    display: block;
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 100%;
    background: black;
    background: linear-gradient(0deg, black 0%, rgba(0, 0, 0, 0) 100%); }

.hero__image {
  width: 100%;
  height: 100%;
  max-height: 650px;
  object-fit: cover; }
